@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@400;700&display=swap);
.book {
    border-radius: 25px;
    background-color: var(--bg);
    box-shadow: var(--shadow);
}

.book:hover {
    background-color: white;
}

.book:hover button {
    background-color: white;
}

/* MENU */

.book-menu {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 8px;
    border-radius: 20px 20px 0 0;
    background-color: var(--dark-bg);
    padding: 16px;
}

.book-read > .book-menu{
    background-color: var(--darker-bg);
}

.book-read > .book-menu > button{
    color: var(--icons-dark);
}


.book-menu > button {
    width: 35px;
    height: 35px;
    border-style: none;
    border-radius: 35px;
    background-color: var(--bg);
    margin: 0 10px 0 0;
    cursor: pointer;
    color: var(--icons);
}

.button-menu > button * {
    cursor: pointer;
}

.book-menu > button:last-child {
    margin: 0;
}

/* INFO */

.book-info {
    color: var(--txt);
    margin: 10px;
    padding: 8px 16px 16px;
}

.book-info > input {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
}

.book-info > div:last-child {
    margin-bottom: 16px;
}

input {
    border-style: none;
    width: 100%;
    background-color: var(--dark-bg);
    font-size: 1rem;
    font-family: "Merriweather Sans",sans-serif;
    color: var(--txt);
}

input[disabled] {
    background-color: transparent;
}


.library {
    padding: 50px;
    grid-area: lib;
    display: grid;
    grid-gap: 31px;
    grid-template-columns: repeat(4, 1fr);
}

.library:hover {
    box-shadow: inset 0 10px 31px 0 rgba(0,0,0,0.08);
}

.button-wrapper {
    margin: 10px;
    padding: 16px;
    display: flex;
    flex-direction: column;
}

.button-wrapper > button {
    margin: auto auto 0 0;
    width: 65px;
    height: 65px;
    border-style: none;
    border-radius: 65px;
    background-color: var(--bg);
    box-shadow: var(--shadow);
    cursor: pointer;
    color: var(--icons);
}

.button-wrapper > button * {
    cursor: pointer;
}

.button-wrapper > button:hover {
    background-color: white;
}

@media screen and (min-width: 600px) {
    .library {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 600px) {
    .library {
        grid-template-columns: 1fr;
    }
}

@media screen and (min-width: 600px) {
    .library {
        grid-template-columns: repeat(1,1fr);
    }
}

@media screen and (min-width: 992px) {
    .library {
        grid-template-columns: repeat(2, 1fr);
    }
}


@media screen and (min-width: 1280px) {
    .library {
        grid-template-columns: repeat(3, 1fr);
    }
}


@media screen and (min-width: 1368px) {
    .library {
        grid-template-columns: repeat(4, 1fr);
    }
}
.app-header {
    grid-area: header;
    display: flex;
    justify-content: space-between;
    background-color: var(--darker-bg);
    padding: 15px 25px;
    border-radius: 33px 33px 0 0;
}

h1 {
    margin: auto auto auto 15px;
    padding: 0;
    color: var(--bg);
    display: flex;
}

h1 > * {
    margin: auto;
}

h1:hover {
    color: white;
}

.app-header > button {
    width: 65px;
    height: 65px;
    border-style: none;
    border-radius: 65px;
    background-color: var(--bg);
    cursor: pointer;
    color: var(--darker-bg);
    box-shadow: var(--shadow);
}

.app-header > button * {
    cursor: pointer;
}

.app-header > button:hover {
background-color: white;
}
.app-header span {
    margin-left: 15px;
}

@media screen and (max-width: 600px) {
    h1 span {
        display: none;
    }
}
.app-navigation {
    grid-area: nav;
    background-color: var(--dark-bg);
    color: var(--txt);
}

.app-navigation > ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.app-navigation li {
    padding: 5px 18px;
    cursor: pointer;
}

.app-navigation li:first-child {
    margin-top: 18px;
}

.app-navigation-selected {
    background-color: var(--darker-bg);
}
footer {
    grid-area: footer;
    padding: 15px 25px;
    border-radius: 0 0 18px 18px;
    background-color: var(--darker-bg);
    color: var(--bg);
    font-weight: bold;
}

footer:hover {
    color: white;
}
.App {
    margin: 80px;
    font-family: "Merriweather Sans",sans-serif;
    display: grid;
    grid-template-areas: 'header header header header header' 'nav lib lib lib lib' 'footer footer footer footer footer';
    border-radius: 33px;
    box-shadow: var(--shadow);
}

@media screen and (max-width: 600px) {
    .App {
        grid-template-areas: 'header' 'nav' 'lib' 'footer';
    }
}
:root {
    --bg: #E6FFFA;
    --dark-bg: #B2F5EA;
    --darker-bg: #4FD1C5;
    --txt: #234E52;

    --icons-dark: #319795;
    --icons: #81E6D9;

    --shadow: 0px 10px 31px 0px rgba(0,0,0,0.08);
}


* {
    box-sizing: border-box;
    transition: all 0.3s ease-in-out;
    transition-property: background-color, box-shadow;
    cursor: default;
}

#root {
    margin: 0;
    padding: 0;
}

body {
    background-color: #E6FFFA;
    background-color: var(--bg);
    margin: 0;
    padding: 0;
}


